import React, { useState } from 'react';
import './style.scss';
import { faCheck, faPlus } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';

export const ItemForm = ({onHandleForm}) => {

  const [textInput, updateTextInput] = useState('');

  const handleInput = (ev) => {
    ev.persist();
    updateTextInput(ev.target.value);
  }

  const handleSubmit = () => {
    if (textInput.trim()) {
      onHandleForm(textInput.trim());
    }
  }

  return (
    <form className='item-form' >
      <div className='item-label'>Add item to track:</div>
      <input className='item-input' type='text' value={textInput} onChange={handleInput} />
      <div className='item-submit' onClick={handleSubmit}><FontAwesomeIcon icon={faCheck} /></div>
    </form>
  );
};
