import { useState, useReducer, useEffect } from 'react';
import { Header } from './components/Header/';
import './App.scss';
import { ItemForm } from './components/ItemForm';
import { TrackingList } from './components/TrackingList';
import { Menu } from './components/Menu';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faSyncAlt,
  faPlusCircle,
  faEllipsisH,
  faTimes,
  faPlus,
  faInfo,
} from '@fortawesome/free-solid-svg-icons';
import { createItem, getDatafromStorage } from './services/item-service';

function App() {
  const initialItem = {
    id: '0123',
    name: 'Take vitamins',
    cycle: {
      type: 'DAILY',
      count: 1,
    },
    isOnEdit: false,
    isChecked: false,
    checkingTimestamp: 0,
  };
  const initialItem2 = {
    id: '45645',
    name: 'do somthing',
    cycle: {
      type: 'DAILY',
      count: 1,
    },
    isOnEdit: false,
    isChecked: true,
    checkingTimestamp: 0,
  };
  

  const [isCreatingItem, updateIsCreatingItem] = useState(false);
  const [menu, setMenu] = useReducer(
    (state, newState) => ({ ...state, ...newState }),
    { isOpeningMenu: false, isMenuOpen: false, isMenuSlide: false }
  );
  const [items, updateItems] = useState([]);

  useEffect(() => {
    const storedItems = getDatafromStorage()
    if (storedItems.length) {
      updateItems(storedItems)
    }
  }, [])

  const handleItem = (id, action, value) => {
    const strItems = JSON.parse(JSON.stringify(items));
    const strItem = strItems.find(item => item.id === id);
    if (action === 'isChecked') {
      strItem[action] = value;
      strItem.isOnEdit = false;
    } else {
      strItem[action] = value;
    }
    updateItems(strItems);
  }

  const handleNewItem = (name) => {
    const newItem = createItem(name);
    updateItems([...items, newItem]);
    updateIsCreatingItem(false);
  }

  const handleIsCreatingItem = boolean => {
    updateIsCreatingItem(boolean);
    handleCloseMenu();
  };

  const handleMenuClick = () => {
    if (menu.isMenuOpen) handleCloseMenu();
    else handleOpenMenu();
  };

  const handleOpenMenu = () => {
    setMenu({ isOpeningMenu: true, isMenuOpen: true });
    setTimeout(() => {
      setMenu({ isMenuSlide: true });
    }, 100);
  };

  const handleCloseMenu = () => {
    setMenu({ isMenuSlide: false });
    setTimeout(() => {
      setMenu({ isOpeningMenu: false, isMenuOpen: false });
    }, 100);
  };

  return (
    <div className='App'>
      <Header />
      {isCreatingItem && (
        <div className='item-form-container'>
          <ItemForm onHandleForm={handleNewItem} />
        </div>
      )}
      {!isCreatingItem && !!items.length && (
        <main className='main'>
          <TrackingList onHandleItem={handleItem} items={items} />
        </main>
      )}
      {!isCreatingItem && !items.length && (
        <div className='initial-create-container'>
          <div
            className='initial-create-button'
            onClick={() => handleIsCreatingItem(true)}
          >
            <FontAwesomeIcon icon={faPlusCircle} size='4x' />
            <div>Create your first item</div>
          </div>
        </div>
      )}
      <Menu
        menu={menu}
        handleMenuClick={handleMenuClick}
        onCreatingItem={handleIsCreatingItem}
        isCreatingItem={isCreatingItem}
      />
    </div>
  );
}

export default App;
