import { v4 as uuidv4 } from 'uuid';

export function getDatafromStorage() {
  try {
    const items = localStorage.getItem('dtItems');
    if (!items || items.length === 0) {
      return [];
    }
    return JSON.parse(items);
  } catch (error) {
    alert('your browser must support Local Storage');
  }
}

function updateStorage(item) {
  let items = getDatafromStorage();
  items = [...items, item];
  localStorage.setItem('dtItems', JSON.stringify(items));
}

export function createItem(name) {
  const newItem = {
    id: uuidv4(),
    name: name,
    cycle: {
      type: 'DAILY',
      count: 1,
    },
    isOnEdit: false,
    isChecked: false,
    checkingTimestamp: 0,
  }

  updateStorage(newItem);
  return newItem;
}