import React from 'react';
import './style.scss';
import { TrackingItem } from '../TrackingItem';

export const TrackingList = ({ items, onHandleItem }) => {

  const handleItem = (id, action, value) => {
    onHandleItem(id, action, value)
  }

  return (
    <div className='tracking-list'>
      {items.map(item => (
        <TrackingItem onHandleItem={handleItem} item={item} key={item.name} />
      ))}
    </div>
  );
};

