import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faSyncAlt,
  faPlusCircle,
  faEllipsisH,
  faTimes,
  faPlus,
  faInfo,
} from '@fortawesome/free-solid-svg-icons';
import './style.scss';

export const Menu = ({
  menu,
  handleMenuClick,
  isCreatingItem,
  onCreatingItem,
}) => {
  
  return isCreatingItem ? (
    <div className='bottom-menu-container'>
      <div
        className='bottom-menu-button close-button' 
        onClick={() => onCreatingItem(false)}
      >
        <FontAwesomeIcon icon={faTimes} />
      </div>
    </div>
  ) : (
    <div className='bottom-menu-container'>
      <div
        className={`bottom-menu-button z-100 ${
          menu.isOpeningMenu ? 'rotate-90' : 'rotate-0'
        }`}
        onClick={handleMenuClick}
      >
        <FontAwesomeIcon icon={faEllipsisH} />
      </div>

      {menu.isMenuOpen && (
        <div
            className={`bottom-menu-button ${menu.isMenuSlide ? 'bottom-75' : ''}`}
            onClick={() => onCreatingItem(true)}
        >
          <FontAwesomeIcon icon={faPlus} />
        </div>
      )}
      {menu.isMenuOpen && (
        <div
          className={`bottom-menu-button ${menu.isMenuSlide ? 'bottom-150' : ''}`}
        >
          <FontAwesomeIcon icon={faInfo} />
        </div>
        )}
        {menu.isMenuOpen && (
          <div className={`bottom-menu-button ${menu.isMenuSlide ? 'right-75' : ''}`}>
            <FontAwesomeIcon icon={faSyncAlt} spin={true} />
          </div>
        )}
    </div>
  );
};
