import React, { useState } from 'react';
import './style.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {
  faCheckCircle,
  faCircle,
  faTimesCircle,
  faTrashAlt,
} from '@fortawesome/free-solid-svg-icons';

export const TrackingItem = ({ onHandleItem, item }) => {
  const [isEdit, updateIsEdit] = useState(false);
  const [isChecked, updateIsChecked] = useState(false);

  const handleItem = (id, action, value) => {
    onHandleItem(id, action, value);
  };

  return (
    <div
      className={`tracking-item-container theme-dark ${
        item.isChecked ? 'color-checked' : 'color-unchecked'
      }`}
    >
      <div
        className='tracking-item'
        onClick={() => handleItem(item.id, 'isOnEdit', !item.isOnEdit)}
      >
        <div>{item.name}</div>
        <FontAwesomeIcon icon={item.isChecked ? faCheckCircle : faCircle} />
      </div>
      <div
        className={`tracking-item-menu ${!item.isOnEdit ? 'hidden-menu' : ''}`}
      >
        <div
          className={`tracking-item-menu-item ${
            !item.isOnEdit ? 'hidden-menu-item' : 'visible-menu-item'
          }`}
          onClick={() => handleItem(item.id, 'isChecked', !item.isChecked)}
        >
          <FontAwesomeIcon
            icon={item.isChecked ? faTimesCircle : faCheckCircle}
          />
        </div>
        <div
          className={`tracking-item-menu-item ${
            !item.isOnEdit ? 'hidden-menu-item' : 'visible-menu-item'
          }`}
        >
          <FontAwesomeIcon icon={faTrashAlt} />
        </div>
      </div>
    </div>
  );
};

